html {
    scroll-behavior: smooth;
}

.header {
    display: flex;
    justify-content: space-between;
    /* position: fixed; */
    width: 100%;
}

.home {
    cursor: pointer;
    margin: 0 25px;
    display: flex;
    align-items: center;
    text-decoration: none;
    color: black;
    font-size: x-large;
    font-weight: 600;
}

.header-options {
    margin: 23px 0px;
    display: flex;
}

.option {
    display: inline-block;
    border-radius: 35px;
    border: 2px solid #9e9ec9;
    padding: 10px 40px;
    margin: 0 10px;
    text-decoration: none;
    color: black;
    background-color: transparent;
    transition: background-color 0.3s ease;
}

.option:hover {
    background-image: linear-gradient(to right, #DBCEF2, #DAEBF3);
}

.option:active {
    background-image: linear-gradient(to right, #C5B3EB, #A7D8E9);
}

.cv-contact {
    display: flex;
    justify-content: space-around;
}


/* =============================================================== */


.menu {
    display: none;
    border: none;
    margin: 0 5px;
}

.menu-span {
    font-size: xx-large;
}

@media (max-width: 960px) {
    .menu {
        display: block;
    }
    .option {
        display: none;
    }
}

@media (max-width: 350px) {
    .home {
        cursor: pointer;
        margin: 0 25px;
        display: flex;
        align-items: center;
        text-decoration: none;
        color: black;
        font-size: large;
        font-weight: 600;
    }
}